import { boolean, select, text, withKnobs } from '@storybook/addon-knobs';
import { storiesOf } from '@storybook/react';
import * as React from 'react';
import { specs } from 'storybook-addon-specifications';
import { Checkbox } from './Checkbox';
import { action } from '@storybook/addon-actions';
import {
  isDefaultChecked,
  isPartiallyChecked,
  isChecked,
  isDisabled,
  position,
  children
} from '../../constants';
import { tests } from './Checkbox.test';

const stories = storiesOf('Checkbox', module);

stories.addDecorator(withKnobs);

stories.add(
  'Playground',
  () => {
     const isChecked = boolean(`isChecked:`, false);
     const isDefaultChecked = boolean(`isDefaultChecked:`, false);
     const isDisabled = boolean(`isDisabled:`, false);
     const isPartiallyChecked = boolean(`isPartiallyChecked:`, false);
     const position = select(`Position:`, ['left', `right`], 'left');
     const children = text(`children:`, `Change me, please ðŸ¥º`);

    const checkbox = (
      <Checkbox
        isChecked={isChecked}
        isDefaultChecked={isDefaultChecked}
        isDisabled={isDisabled}
        isPartiallyChecked={isPartiallyChecked}
        position={position}
        onChange={action(`onChange`)}
      >
        {children}
      </Checkbox>
    );

    specs(() => tests);

    return checkbox;
  },
  {
    info: { inline: true }
  }
);
